            <h1 class="display-3 section-title color">Me, Myself & I</h1>
            <div class="row g-5 mt-1">
                <div class="col-lg-5 left-side">
                    <p class="about-text">
                        I'm an,
                        <span class="keyword">AUCian</span> & <span class="keyword">ex-STEMer</span> software developer based
