                        continuing this work studying <span class="keyword">Computer Engineering</span> @
                        <!-- <span class="DCollege"> -->
                        the American University in Cairo.
                        <!-- </span> -->
                        I love Hip-Hop Music as well as E-Sports. Also, I'm a big Marvel fan, especially <em>Spidey</em>.
                    </p>
